import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './header.scss';
import { LogoIcon, SearchIcon, WriteIcon, SaveIcon, NotificationsIcon } from '../../icons';
import Menu from '@mui/material/Menu';
import Avatar from '@mui/material/Avatar';

export const Header = ({ handleSearch }) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [user, setUser] = useState(null);
  const [anchorElUser, setAnchorElUser] = useState(null);
  const handleChange = (event) => {
    setSearchTerm(event.target.value);
    handleSearch(event.target.value);
  };
  useEffect(() => {
    const storedUser = localStorage.getItem("user");
    if (storedUser) {
      setUser(JSON.parse(storedUser));
    }
  }, []);

  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };
  const logout = () => {
    localStorage.removeItem("user");
    setUser(null);
  };
  return (
    <header>
      <nav className='nav-left'>
        <Link to={'/falts/'} style={{ textDecoration: 'none', height: '24px' }}>
          <LogoIcon />
        </Link>
        <div className='search'>
          <SearchIcon />
          <input
            type="text"
            placeholder='...'
            value={searchTerm}
            onChange={handleChange}
          />
        </div>
      </nav>
      {user ? (
        <nav className='nav-right'>
          <Link to={'/falts/write'}>
            <li>
              <WriteIcon />
              <p></p>
            </li>
          </Link>
          <li>
            <SaveIcon />
          </li>
          <li>
            <NotificationsIcon />
          </li>
          <li>
            <Avatar onClick={handleOpenUserMenu} src={user?.image} />
            <Menu
              sx={{ mt: '45px' }}
              id="menu-appbar"
              anchorEl={anchorElUser}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(anchorElUser)}
              onClose={handleCloseUserMenu}
            >
              <div className='user-menu'>
                <p onClick={handleCloseUserMenu}>{user?.fullName}</p>
                <p onClick={handleCloseUserMenu}>Mo </p>
                <p onClick={logout}></p>
              </div>
            </Menu>
          </li>
        </nav>
      ) : (
        <nav className='nav-right-unlogged'>
          <li> </li>
          <li></li>
          <Link to={'/falts/login'}>
            <li></li>
          </Link>
          <Link to={'/falts/registration'}>
            <li className='btn'>
              
            </li>
          </Link>
        </nav>
      )}
    </header>
  );
};
